@import url(https://fonts.googleapis.com/css2?family=DotGothic16&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
  position: fixed;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  width: 100vw;
  height: 100vh;
  background-color: black;
  display: flex;
  align-items: center;
  justify-content: center;
}

.App{display:flex;flex-direction:row;align-items:center;justify-content:center;color:white;width:100vw;height:100vh;background-color:black;letter-spacing:1px;font-family:"DotGothic16", sans-serif;box-sizing:border-box;overflow:hidden}div,p{box-sizing:border-box;margin:0;padding:0}

.sideBar{display:flex;flex-direction:column;align-items:center;justify-content:center;width:30%;height:50%;border-right:1px solid white;font-size:24px}.sideBar div{transition:0.5s all;margin:8px 0;color:#333333;cursor:pointer}.sideBar div:hover{color:white}.sideBar .active{color:white}@media (max-width: 734px){.sideBar{display:none}}.spSideBar{display:flex;flex-direction:column;align-items:center;justify-content:center;position:fixed;top:0;bottom:0;left:0;right:0;z-index:6;background-color:white;color:black}.spSideBar div{font-size:24px;margin:8px 0;-webkit-animation-name:fadeIn;animation-name:fadeIn;-webkit-animation-duration:0.5s;animation-duration:0.5s;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards}@-webkit-keyframes fadeIn{from{-webkit-transform:scale(1.2);transform:scale(1.2);opacity:0}to{opacity:1;-webkit-transform:scale(1);transform:scale(1)}}@keyframes fadeIn{from{-webkit-transform:scale(1.2);transform:scale(1.2);opacity:0}to{opacity:1;-webkit-transform:scale(1);transform:scale(1)}}

.status{height:100vh;width:70%;padding:64px;overflow-y:scroll}.basicInfo{display:flex;flex-direction:row;align-items:center;justify-content:center}.basicInfo .photo{background-repeat:no-repeat;background-size:cover;background-position:center center;height:150px;width:150px;margin-right:16px;background-image:url(data:image/gif;base64,)}.basicInfo .textContainer{width:calc(100% - 200px)}.basicInfo .textContainer p{display:flex;flex-direction:row;align-items:flex-start;justify-content:space-between;padding-bottom:8px}@media (max-width: 734px){.status{width:100%;padding:32px 16px}.basicInfo .textContainer{width:100%;flex-shrink:3}.basicInfo .textContainer span{text-align:right}}

.card{opacity:0;border:1px solid white;width:100%;padding:16px;padding-top:36px;position:relative;margin-bottom:32px;-webkit-animation-name:card;animation-name:card;-webkit-animation-duration:1s;animation-duration:1s;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards}.card:last-child{margin-bottom:0}.card .header{font-size:16px;position:absolute;background-color:white;height:20px;line-height:20px;width:30%;top:-1px;left:-10px;color:black;font-weight:700;-webkit-transform:skewX(-30deg);transform:skewX(-30deg);padding-left:16px}.card:nth-child(1){-webkit-animation-delay:0s;animation-delay:0s}.card:nth-child(2){-webkit-animation-delay:0.3s;animation-delay:0.3s}.card:nth-child(3){-webkit-animation-delay:0.6s;animation-delay:0.6s}@-webkit-keyframes card{from{-webkit-transform:translateX(-10%);transform:translateX(-10%);opacity:0}to{-webkit-transform:translateX(0);transform:translateX(0);opacity:1}}@keyframes card{from{-webkit-transform:translateX(-10%);transform:translateX(-10%);opacity:0}to{-webkit-transform:translateX(0);transform:translateX(0);opacity:1}}

.skill{display:flex;flex-direction:row;align-items:center;justify-content:flex-start;margin-bottom:8px}.skill .icon{display:flex;flex-direction:row;align-items:center;justify-content:center;height:40px;width:40px;border:1px solid white;border-radius:5px;flex-shrink:0;margin-right:16px}.skill .isHover{transition:0.5s all}.skill .isHover:hover{color:black;background-color:white}.skill .aLink{color:inherit;text-decoration:none}.skill .container{display:flex;flex-direction:column;align-items:flex-start;justify-content:center}.skill .container .title{font-weight:700;font-size:16px}.skill .container .content{font-size:14px}

.profile{height:100vh;width:70%;padding:64px;overflow-y:hidden}.tree{position:relative;width:100%}@media (max-width: 734px){.profile{width:100%;padding:32px 16px;overflow-y:scroll}}.noMargin{margin-bottom:0}

.link{width:2px;background-color:white;position:absolute;left:30%;-webkit-animation-name:linkAni;animation-name:linkAni;-webkit-animation-duration:0.5s;animation-duration:0.5s;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards}.link:last-child{-webkit-animation-name:lastLinkAni;animation-name:lastLinkAni}.dot{z-index:5;-webkit-transform:scale(0);transform:scale(0);border-radius:50%;background-color:black;border:2px solid white;position:absolute;left:calc(30% - 7px);width:16px;height:16px;-webkit-animation-name:dotAni;animation-name:dotAni;-webkit-animation-duration:0.5s;animation-duration:0.5s;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards}.linkAni1{top:0;-webkit-animation-delay:0.5s;animation-delay:0.5s}.linkAni2{top:100px;-webkit-animation-delay:2s;animation-delay:2s}.linkAni3{top:200px;-webkit-animation-delay:3.5s;animation-delay:3.5s}.linkAni4{top:300px;-webkit-animation-delay:5s;animation-delay:5s}.linkAni5{top:400px;-webkit-animation-delay:6.5s;animation-delay:6.5s}.dotAni1{top:92px;-webkit-animation-delay:1s;animation-delay:1s}.dotAni2{top:192px;-webkit-animation-delay:2.5s;animation-delay:2.5s}.dotAni3{top:292px;-webkit-animation-delay:4s;animation-delay:4s}.dotAni4{top:392px;-webkit-animation-delay:5.5s;animation-delay:5.5s}.dotAni5{top:492px;-webkit-animation-delay:7s;animation-delay:7s}@-webkit-keyframes linkAni{from{height:0}to{height:100px}}@keyframes linkAni{from{height:0}to{height:100px}}@-webkit-keyframes dotAni{from{-webkit-transform:scale(0);transform:scale(0)}to{-webkit-transform:scale(1);transform:scale(1)}}@keyframes dotAni{from{-webkit-transform:scale(0);transform:scale(0)}to{-webkit-transform:scale(1);transform:scale(1)}}@-webkit-keyframes lastLinkAni{from{height:0}to{height:300px}}@keyframes lastLinkAni{from{height:0}to{height:300px}}.itemRight{opacity:0;position:absolute;left:35%;-webkit-animation-name:item;animation-name:item;-webkit-animation-duration:0.5s;animation-duration:0.5s;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards}.itemLeft{opacity:0;position:absolute;text-align:right;right:75%;-webkit-animation-name:item;animation-name:item;-webkit-animation-duration:0.5s;animation-duration:0.5s;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards}.itemLeftAni1{top:88px;-webkit-animation-delay:1.5s;animation-delay:1.5s}.itemRightAni1{top:80px;-webkit-animation-delay:1.5s;animation-delay:1.5s}.itemLeftAni2{top:188px;-webkit-animation-delay:3s;animation-delay:3s}.itemRightAni2{top:180px;-webkit-animation-delay:3s;animation-delay:3s}.itemLeftAni3{top:288px;-webkit-animation-delay:4.5s;animation-delay:4.5s}.itemRightAni3{top:280px;-webkit-animation-delay:4.5s;animation-delay:4.5s}.itemLeftAni4{top:388px;-webkit-animation-delay:6s;animation-delay:6s}.itemRightAni4{top:380px;-webkit-animation-delay:6s;animation-delay:6s}@-webkit-keyframes item{from{opacity:0}to{opacity:1}}@keyframes item{from{opacity:0}to{opacity:1}}

.project{height:100vh;width:70%;padding:64px;overflow-y:scroll;color:white}.project .image1{background-image:url(/static/media/mota.00fcd76e.png)}.project .image2{background-image:url(/static/media/animeFilter.1c3a90f7.png)}.project .image3{background-image:url(/static/media/pants.8cda1507.png)}.project .image4{background-image:url(/static/media/streamer.cf4bf422.png)}.project .aLink{text-decoration:underline}@media (max-width: 734px){.project{width:100%;padding:32px 16px}}

.projectItem{width:100%;border:1px solid white;border-radius:5px;margin-bottom:32px;-webkit-animation-name:projectItem;animation-name:projectItem;-webkit-animation-duration:1s;animation-duration:1s;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards}.projectItem .image{background-repeat:no-repeat;background-size:cover;background-position:center center;width:100%;height:400px;border-radius:5px;border-bottom-left-radius:0px;border-bottom-right-radius:0px}.projectItem .text{border-top:1px solid;padding:16px;position:relative}.projectItem .title{font-weight:700;font-size:16px}.projectItem .content{font-size:14px}.projectItem .linkItem{display:flex;flex-direction:row;align-items:center;justify-content:center;transition:0.5s all;position:absolute;right:10%;top:-14px;background-color:white;border-radius:50%;height:24px;width:24px;color:black;cursor:pointer}.projectItem .linkItem:hover{opacity:0.9}.projectItem .aLink{color:inherit;cursor:pointer;font-weight:bold;text-decoration:underline}.projectItem:nth-child(1){-webkit-transform:translateY(-100%);transform:translateY(-100%)}.projectItem:nth-child(2){-webkit-transform:translateY(-200%);transform:translateY(-200%)}.projectItem:nth-child(3){-webkit-transform:translateY(-300%);transform:translateY(-300%)}.projectItem:nth-child(4){-webkit-transform:translateY(-400%);transform:translateY(-400%)}@-webkit-keyframes projectItem{from{opacity:0}to{-webkit-transform:translateY(0);transform:translateY(0);opacity:1}}@keyframes projectItem{from{opacity:0}to{-webkit-transform:translateY(0);transform:translateY(0);opacity:1}}

.navBar{display:flex;flex-direction:row;align-items:center;justify-content:center;transition:0.5s all;opacity:0.9;z-index:10;height:44px;width:50px;background-color:white;color:black;position:fixed;bottom:16px;right:16px;-webkit-clip-path:polygon(25% 0%, 75% 0%, 100% 50%, 75% 100%, 25% 100%, 0% 50%)}.isMenuOpen{background-color:black;color:white}

.loading{display:flex;flex-direction:column;align-items:center;justify-content:center;position:fixed;top:0;bottom:0;left:0;right:0;z-index:20;background-color:black;color:white;-webkit-animation-name:loading;animation-name:loading;-webkit-animation-duration:3s;animation-duration:3s;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards}@-webkit-keyframes loading{50%{opacity:1}99%{opacity:0;z-index:20}100%{opacity:0;display:none;z-index:-1}}@keyframes loading{50%{opacity:1}99%{opacity:0;z-index:20}100%{opacity:0;display:none;z-index:-1}}.loadingText{opacity:0;-webkit-animation-name:loadingText;animation-name:loadingText;-webkit-animation-duration:1s;animation-duration:1s;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards;-webkit-animation-iteration-count:2;animation-iteration-count:2}@-webkit-keyframes loadingText{0%{opacity:0}50%{opacity:1}100%{opacity:0}}@keyframes loadingText{0%{opacity:0}50%{opacity:1}100%{opacity:0}}.underLine{-webkit-animation-name:fillLine;animation-name:fillLine;-webkit-animation-duration:1.5s;animation-duration:1.5s;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards;height:1px;background-color:white}@-webkit-keyframes fillLine{0%{width:0}100%{width:100%}}@keyframes fillLine{0%{width:0}100%{width:100%}}

